import Container from "../components/container"
import * as React from "react"
import { graphql } from "gatsby"
import Img from "gatsby-image"
import Header from "../components/header"
import Footer from "../components/footer"
import { FiCamera } from '@react-icons/all-files/fi/FiCamera';
import { RiCameraLensFill } from '@react-icons/all-files/ri/RiCameraLensFill';
import { RiImageEditLine } from '@react-icons/all-files/ri/RiImageEditLine';

export default function About({data}) {
  return (
    <Container>
      <Header headerTitle="abouttitle"></Header>
      <section className="hero">
        <div className="hero-body">
          <p className="title">
            #about
          </p>
          <p className="subtitle">
            my interests, my work
          </p>
        </div>
      </section>
      <section className="section">
        <div className="content">
          <div className="columns">
            <div className="column">
              <Img fixed={data.file.childImageSharp.fixed} />
            </div>
            <div className="column">
              <p className="content">
                Hello, i'm an italian based web developer passionate about photography, mostly landscape, currently shooting with my Fuji X-T3 equipped with 16mm and 90mm.
              </p>
              <p className="content">
                <FiCamera /> FUJI X-T3 • FUJI X-T20<br />
                <RiCameraLensFill /> 16mm • 90mm • 18-135mm<br />
                <RiImageEditLine /> Capture One
              </p>
            </div>
          </div>
        </div>
      </section>
      <Footer></Footer>
    </Container>
  )
}

export const query = graphql`
  query {
    file(relativePath: { eq: "profile.jpg" }) {
      childImageSharp {
        # Specify the image processing specifications right in the query.
        # Makes it trivial to update as your page's design changes.
        fixed(width: 1280) {
          ...GatsbyImageSharpFixed
        }
      }
    }
  }
`
